export default "\nconst addTogglePrevNextBtnsActive = (emblaApi, prevBtn, nextBtn) => {\n    const togglePrevNextBtnsState = () => {\n        if (emblaApi.canScrollPrev())\n            prevBtn.removeAttribute('disabled');\n        else\n            prevBtn.setAttribute('disabled', 'disabled');\n        \n        if (emblaApi.canScrollNext())\n            nextBtn.removeAttribute('disabled');\n        else\n            nextBtn.setAttribute('disabled', 'disabled');\n    };\n    \n    emblaApi\n        .on('select', togglePrevNextBtnsState)\n        .on('init', togglePrevNextBtnsState)\n        .on('reInit', togglePrevNextBtnsState);\n    \n    return () => {\n        prevBtn.removeAttribute('disabled');\n        nextBtn.removeAttribute('disabled');\n    };\n};\n\nexport const addPrevNextBtnsClickHandlers = (emblaApi, prevBtn, nextBtn, \n/*__NAV_AUTOPLAY_REPLACE_START__*/\nonButtonClick\n/*__NAV_AUTOPLAY_REPLACE_END__*/\n) => {\n    const scrollPrev = () => {\n        emblaApi.scrollPrev();\n        /*__NAV_AUTOPLAY_REPLACE_START__*/\n        if (onButtonClick)\n            onButtonClick(emblaApi);\n        /*__NAV_AUTOPLAY_REPLACE_END__*/\n    };\n    const scrollNext = () => {\n        emblaApi.scrollNext();\n        /*__NAV_AUTOPLAY_REPLACE_START__*/\n        if (onButtonClick)\n            onButtonClick(emblaApi);\n        /*__NAV_AUTOPLAY_REPLACE_END__*/\n    };\n    prevBtn.addEventListener('click', scrollPrev, false);\n    nextBtn.addEventListener('click', scrollNext, false);\n    \n    const removeTogglePrevNextBtnsActive = addTogglePrevNextBtnsActive(emblaApi, prevBtn, nextBtn);\n    \n    return () => {\n        removeTogglePrevNextBtnsActive();\n        prevBtn.removeEventListener('click', scrollPrev, false);\n        nextBtn.removeEventListener('click', scrollNext, false);\n    };\n};\n";